import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import './css/ProjectCard.css'

function ProjectCard(props){
    const {title, description, link, language} = props.project
    let didMounted = false

    useEffect(() =>{
        const cards = document.querySelectorAll('.project-card')
        cards.forEach(card => {
            card.className = 'project-card'
        })
    }, [])

    return(
        <React.Fragment>
            <div className='cardArea'>
                <div className={didMounted ? 'project-card' : 'project-card hide'} onClick={() => {openInNewTab(link)}}>
                    <p className='p-language'>{language}</p>
                    <h1 className='p-title'>{title}</h1>
                    <p className='p-description'>{description}</p>
                    <p className='p-link'>click to visit!</p>
                </div>
            </div>
        </React.Fragment>
    )
}

function openInNewTab(href) {
    Object.assign(document.createElement('a'), {
        target: '_blank',
        href: href,
        rel: 'noopener noreferrer'
    }).click();
}

export default ProjectCard